import {useParams} from "react-router-dom";
import {useEffect, useState} from "react";
import style from "./Detail.module.css";
import {Link} from "react-router-dom";

function Detail(){
    const {id} = useParams();
    const [loading, setLoading] = useState(true);
    const [movie, setMovie] = useState({});

    const getMovie = async() => {
        const json = await (await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)).json();
        setMovie(json.data.movie);
        setLoading(false);
    }

    useEffect(() => {
        getMovie();
    }, []);    
    console.log(movie);
    return (
        <div>
            {loading ? <h2>loading</h2> : (
                <div className={style.movieBox}>
                    <h2>{movie.title}</h2>
                    <div>개봉년도 : {movie.year}년</div>
                    <div>런타임 : {Math.floor(movie.runtime / 60)}시간 {Math.floor(movie.runtime % 60)}분</div>
                    <div>평점 : {movie.rating} / 10</div>
                    <div className={style.genres}>
                        <span className={style.inline}>장르 :</span>
                        <ul className={style.floatUl}>
                            {movie.genres.map((g, idx)=>(
                                <li
                                    key={idx}
                                    className={style.li}
                                >
                                    {g}
                                </li>
                            ))}
                        </ul>
                    </div>
                    <div className={style.clear}></div>
                    <img src={movie.medium_cover_image} />                    
                    <div>
                        줄거리 : <br/>
                        {movie.description_full}
                    </div>
                    <button type="button" className={style.defaultBtn}>
                        <Link to={"/"}>목록으로</Link>
                    </button>
                </div>
            )}
        </div>
    );
}
export default Detail;